	module.exports = {"data":{"markdownRemark":{"html":"<p>「本当の流行を創り出したいのなら、人と全く逆のことをやりなさい」\n昔言われたこの教えを色々履き違えた結果がこれ。</p>\n<p>尤もこの言葉は間違いではないと思うし、その証拠に今でも信じている。\n流行は常識を知り、常識をぶち破った中から生まれる。この考え方は至極正しいと思う。</p>\n<p>この絵の最大の問題点は、<em>常識を大してぶち破っていないこと</em>だ。</p>","frontmatter":{"cover":null,"date":"1994-11-21T10:00:49+09:00","strDate":"1994/11/21","title":"クリーチャー"}}},"pathContext":{"path":"/ja/blog/1994/11/21-creature/","langKey":"ja"}}

